//Vue3 update

<template>
    <div class='row m-1'>
        <download-image-button v-if='showDownload' :parentComponentName="getParentComponentName" :parentCanvasID="getCanvasID" :filename="filename"/>
        <button type='button' class='btn btn-secondary zoom-button me-1' id='zoom-plus-button' @click='zoomIn'>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-zoom-in" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M6.5 12a5.5 5.5 0 1 0 0-11 5.5 5.5 0 0 0 0 11zM13 6.5a6.5 6.5 0 1 1-13 0 6.5 6.5 0 0 1 13 0z"/>
                <path d="M10.344 11.742c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1 6.538 6.538 0 0 1-1.398 1.4z"/>
                <path fill-rule="evenodd" d="M6.5 3a.5.5 0 0 1 .5.5V6h2.5a.5.5 0 0 1 0 1H7v2.5a.5.5 0 0 1-1 0V7H3.5a.5.5 0 0 1 0-1H6V3.5a.5.5 0 0 1 .5-.5z"/>
            </svg>
        </button>

        <button type='button' class='btn btn-secondary zoom-button me-1' id='zoom-minus-button' @click='zoomOut'>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-zoom-out" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M6.5 12a5.5 5.5 0 1 0 0-11 5.5 5.5 0 0 0 0 11zM13 6.5a6.5 6.5 0 1 1-13 0 6.5 6.5 0 0 1 13 0z"/>
                <path d="M10.344 11.742c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1 6.538 6.538 0 0 1-1.398 1.4z"/>
                <path fill-rule="evenodd" d="M3 6.5a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5z"/>
            </svg>
        </button>


        <label class='m-2' for="filename-input">Filename: 'webcam-view-' + inputted text below</label>
        <input type='text' id='filename-input' v-model='filename'>
        
    </div>
</template>

<script>

import DownloadImageButton from './DownloadImageButton.vue';


export default {

  name: 'Toolbar',
  props:{
      'parentComponentName': String, 
      'parentCanvasID': String,
      'parentDivID': String,
      'showDownload': Boolean,
      'showPopupHelp': Boolean,
  },
  emits:['zoom'],
  data () {
    return {
        filename: '',
        zoomLevel: 2, //1,2,3,4 -> corresponds to col-3, col-4, col-6, col-9, col-12
    }
  },
  components: {
    DownloadImageButton,
  },
  created(){
      
  },
  mounted(){
      

  },
  computed:{
      getParentComponentName(){
          return this.parentComponentName;
      },
      getCanvasID(){
          return this.parentCanvasID;
      },
      getParentDivID(){
          return this.parentDivID;
      }
      
  },
  watch:{
      
  },
  methods: {
      zoomIn(){
          if(this.zoomLevel < 4){
              this.zoomLevel += 1;
          }
          if(this.zoomLevel == 1){
              this.$emit('zoom', 'col-4')
          } else if(this.zoomLevel == 2){
              this.$emit('zoom', 'col-6')
          } else if(this.zoomLevel == 3){
              this.$emit('zoom', 'col-9')
          } else if(this.zoomLevel == 4){
              this.$emit('zoom', 'col-12')
          } 
          
      },
      zoomOut(){
          if(this.zoomLevel > 0){
              this.zoomLevel -= 1;
          }
          if(this.zoomLevel == 0){
              this.$emit('zoom', 'col-3')
          } else if(this.zoomLevel == 1){
              this.$emit('zoom', 'col-4')
          } else if(this.zoomLevel == 2){
              this.$emit('zoom', 'col-6')
          } else if(this.zoomLevel == 3){
              this.$emit('zoom', 'col-9')
          } 
      }
      
  }
}
</script>

<style scoped>
.zoom-button{
    max-height:40px;
    max-width:40px;
}

</style>