//Vue3 updated

<template>

<div v-if='getSessionExpired' class='row'>
  <div class='col-12'>
        <img id='session-end-image' src='https://assets.practable.io/images/common/thank-you-screen.svg' alt='session ended'>
    </div>
</div>
<div v-else>
  <div class='row align-items-center justify-content-center' >
      <div :class='zoomClass' id='video-element'>
        <canvas id="video-canvas"></canvas>
        <toolbar parentCanvasID="video-canvas" parentComponentName="webcam-view" parentDivID='video-element' :showDownload='true' :showPopupHelp='false' @zoom="setZoomClass"></toolbar>
      </div>
    </div>
</div>    
    
    
    <!-- <div id='videoWrapper'></div> -->
</template>

<script>
import JSMpeg from "@cycjimmy/jsmpeg-player";
import { mapGetters } from 'vuex';
import Toolbar from "./Toolbar.vue";

export default {
  name: "VideoElement",
  props: ["url"],
  components:{
      Toolbar,
    },
  data(){
      return{
        player: null,
        zoomClass: 'col-6'
      }
    },
  computed:{
    ...mapGetters([
      'getSessionExpired',
    ]),
  },
  watch:{
    url(){
        let canvas = document.getElementById("video-canvas");
        this.player = new JSMpeg.Player(this.url, {canvas: canvas, preserveDrawingBuffer: true});

    },
  },
  methods:{
    setZoomClass(zoomLevel){
        this.zoomClass = zoomLevel;
    }
  }
    
};


</script>

<style>
    #video-canvas {
      width: 100%;
      /* height: 100%; */
    }

    #session-end-image {
      width: 100%;
      /* height: 100%; */
    }

</style>